<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/youtube')">유튜브</li>
			<li @click="goto('/admin/column')">
				칼럼
			</li>
			<li @click="goto('/admin/writer')" class="current">칼럼 작성자</li>
		</ul>

		<div class="contents-container">
			<p class="contents-title underline marB-20 body3">칼럼 작성자 등록</p>
			<div class="input-container marB-40">
				<section class="basic-info">
					<div class="input-item">
						<p class="small-title">이름</p>
						<input
							type="text"
							class="admin-input small"
							name="name"
							placeholder="이름"
							v-model="name"
						/>
					</div>

					<div class="input-item">
						<p class="small-title">직업</p>
						<input
							type="text"
							class="admin-input small"
							name="job"
							placeholder="직업"
							v-model="job"
						/>
					</div>

					<div class="input-item">
						<p class="small-title">상세 설명</p>
						<input
							type="text"
							class="admin-input large"
							name="contents"
							v-model="contents"
						/>
					</div>
				</section>
				<LoadingSpinner :loading="isLoading" />
				<button class="outlined-btn" @click="addWriter">등록하기</button>
			</div>
		</div>
	</div>
</template>

<script>
import { addColumnWriter } from '@/api/column';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: { LoadingSpinner },
	data: () => ({
		name: '',
		job: '',
		contents: '',
		errMsg: '',
		isLoading: false,
	}),
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async addWriter() {
			if (!this.name) {
				this.errMsg = '이름을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.job) {
				this.errMsg = '직업을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.contents) {
				this.errMsg = '내용을 입력해주세요';
				alert(this.errMsg);
				return false;
			}

			let data = {
				name: this.name,
				contents: this.contents,
				job: this.job,
			};

			let result;
			this.isLoading = true;
			try {
				result = await addColumnWriter(data);
				if (result.status == 200) {
					alert('작성자가 등록되었습니다');
					await this.initForm();
				} else {
					alert('에러가 발생했습니다');
				}
			} catch (e) {
				alert('에러가 발생했습니다');
			} finally {
				this.isLoading = false;
			}
		},
		initForm() {
			this.name = '';
			this.job = '';
			this.contents = '';
		},
	},
};
</script>

<style></style>
